'use client';

import { useState, useEffect } from 'react';
import { ChevronUp } from 'lucide-react';
import { Button } from '@/components/ui/button';

export default function BackToTop() {
  const [isVisible, setIsVisible] = useState(false);

  // ÁõëÂê¨ÊªöÂä®‰∫ã‰ª∂
  useEffect(() => {
    const handleScroll = () => {
      // ÂΩìÈ°µÈù¢ÊªöÂä®Ë∂ÖËøá 300px Êó∂ÊòæÁ§∫ÊåâÈíÆ
      if (window.scrollY > 300) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // ÂõûÂà∞È°∂ÈÉ®ÂáΩÊï∞
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  if (!isVisible) {
    return null;
  }

  return (
    <Button
      onClick={scrollToTop}
      className="fixed bottom-8 right-8 z-50 w-12 h-12 rounded-full shadow-lg hover:shadow-xl transition-all duration-300 gradient-primary text-white p-0"
      aria-label="ÂõûÂà∞È°∂ÈÉ®"
    >
      <ChevronUp className="h-6 w-6" />
    </Button>
  );
} 